<template>
  <div class="search-container">

    <div class="keyword-wrapper">
      <h4 class="keyword">关键词:</h4>
    </div>

    <div>
      <input v-model="searchText" type="text" class="search-box" placeholder="" />
      <button class="search-button" @click="fetchData()">
        <i class="fas fa-search"></i>
      </button>
    </div>

    <h2 class="name-list-title">名称列表</h2>

    <div class="square-box">
      <div class="name-list">
        <h2 class="name-title">名称</h2>
        <ul>
          <li v-for="(item, index) in displayedNames" :key="index" :class="{ 'highlighted': highlightedIndex === index }"
            @mouseover="highlightName(index)" @mouseleave="clearHighlight()" @click="getPDFPreview(item.article_name)">
            <span class="name">{{ item.article_name }}</span>
          </li>
        </ul>
        <div class="pagination">
          <button @click="changePage(-1)" :disabled="currentPage === 0">Previous</button>
          <span>Page {{ currentPage + 1 }} of {{ totalPages }}</span>
          <button @click="changePage(1)" :disabled="currentPage === totalPages - 1">Next</button>
        </div>
      </div>
    </div>
    <h2 class="file-list-title">文档阅读区</h2>
    <div class="side-box">
      <div>
        <div ref="pdfContainer" style="overflow: scroll; height: 500px"></div>
      </div>
    </div>
  </div>
</template>
      
<script>
import * as PDFJS from 'pdfjs-dist';
import axios from 'axios';
import 'pdfjs-dist/build/pdf.worker.entry';

const pdfApi = 'http://localhost:8080/'
export default {
  data() {
    return {

      searchText: "", //绑定的数据
      pafPath: "",
      pdfUrl: "",
      nameList: [
      ],
      itemsPerPage: 5, // Number of items to display per page
      currentPage: 0, // Current page index
      highlightedIndex: -1 // 高亮的名称索引
    };
  },

  computed: {
    totalPages() {
      return Math.ceil(this.nameList.length / this.itemsPerPage);
    },
    displayedNames() {
      const startIndex = this.currentPage * this.itemsPerPage;
      const endIndex = startIndex + this.itemsPerPage;
      return this.nameList.slice(startIndex, endIndex);
    },
  },

  mounted() {
  },
  methods: {
    highlightName(index) {
      this.highlightedIndex = index;
    },
    clearHighlight() {
      this.highlightedIndex = -1;
    },
    fetchData() {
      axios.get('http://localhost:8888/api/search/getInfo', {
        params: {
          key: this.searchText
        }
      })
        .then(response => {
          let info = response.data
          this.nameList.length = 0
          info.forEach(element => {
            this.nameList.push(element)
          });
          console.log(info)
        })
        .catch(error => {
          // 处理错误
          console.error(error);
        });
    },
    changePage(direction) {
      if (direction === -1 && this.currentPage > 0) {
        this.currentPage--;
      } else if (direction === 1 && this.currentPage < this.totalPages - 1) {
        this.currentPage++;
      }
    },
    getPDFPreview(name) {
      //对pdfUrl进行转义
      this.pdfUrl = encodeURI(pdfApi + name + '.pdf');
      console.log(this.pdfUrl)
      this.loadPDF(this.pdfUrl);
    },
    async loadPDF(pdfUrl) {
      console.log('LOAD PDF', pdfUrl);
      const container = this.$refs.pdfContainer;
      container.innerHTML = '';
      const loadingTask = PDFJS.getDocument(pdfUrl);
      const pdf = await loadingTask.promise;
      const numPages = pdf.numPages;

      for (let pageNum = 1; pageNum <= numPages; pageNum++) {
        const page = await pdf.getPage(pageNum);
        const scale = 1.0;
        const viewport = page.getViewport({ scale });
        const canvas = document.createElement('canvas');
        const context = canvas.getContext('2d');
        canvas.height = viewport.height;
        canvas.width = viewport.width;
        container.appendChild(canvas);
        await page.render({ canvasContext: context, viewport });
      }
    },
  }
}  
</script>
      
